// .advantage .advantage .advantage .advantage .advantage .advantage .advantage .advantage
.advantage {
	margin-top: 100px;
	margin-bottom: 100px;
	&__wrapp {
		display: flex;
		justify-content: space-between;
		margin: 0 -30px;
		max-width: 1175px;
		margin: 0 auto;
	}
	&__item {
		flex: 0 1 246px;
		display: flex;
		flex-direction: column;
		align-items: center;
		max-width: 246px;
		min-height: 246px;
		padding: 32px;
		margin: 0 30px;
		background: #ffffff;
		box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
		border-radius: 25px;
	}
	&__img {
		position: relative;
		height: 120px;
		width: 120px;
		& img {
			position: absolute;
			object-fit: contain;
			width: 100%;
			height: 100%;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
		}
	}
	&__title {
		margin: 9px 0 10px 0;
		font-weight: 700;
		font-size: 16px;
		line-height: 20px;
		color: #000000;
	}
	&__descr {
		font-weight: 400;
		font-size: 14px;
		line-height: 17px;
		text-align: center;
		color: #808080;
		text-align: center;
	}
}
