// .modifier .modifier .modifier .modifier .modifier .modifier .modifier .modifier .mod

.modifier {
	&__wrap {
	}
	&__roof {
	}
	&__img {
		margin: 40px 0;
		padding-bottom: 28%;
		position: relative;
		width: 100%;
		& img {
			position: absolute;
			left: 0;
			height: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__result {
		margin-top: 90px;
	}
}

.modifier__wall .config {
	&__item {
		margin-left: 0;
		margin-right: 25px;
	}
}

.config {
	&__wrapp {
		display: flex;
		&_roof {
			justify-content: end;
		}
	}
	&__item {
		display: flex;
		align-items: center;
		max-width: 290px;
		border-radius: 128px;
		padding: 13px 0;
		margin-left: 25px;
		transition: all 0.2s ease-in-out;
		&_wall {
			flex-direction: row-reverse;
		}
	}
	&__text-block {
		text-align: right;
		max-width: 152px;
		margin-right: 21px;
		&_revers {
			text-align: right;
		}
	}
	&__title {
		font-weight: 500;
		font-size: 0px;
		opacity: 0%;
		line-height: 19px;
		color: #545454;
		margin-bottom: 8px;
		transition: all 0.2s ease-in-out;
	}

	&__descr {
		font-weight: 400;
		font-size: 0px;
		opacity: 0%;
		line-height: 16px;
		color: #999999;
		transition: all 0.2s ease-in-out;
	}

	&__img {
	}
}

.config__item:hover {
	padding: 13px 21px;
	background: #ffffff;
	box-shadow: 0px 3.81789px 16.5442px rgba(176, 176, 176, 0.35);
}

.config__item:hover .config__title {
	font-size: 16px;
	opacity: 100%;
}

.config__item:hover .config__descr {
	font-size: 14px;
	opacity: 100%;
}

.result {
	&__wrap {
		display: flex;
		margin: 0 -25px;
	}
	&__item {
		padding: 40px 53px;
		margin: 0 25px;
		display: flex;
		flex: 0 1 50%;
		background: #ffffff;
		box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
		border-radius: 50.9051px;
		&_rewers {
			flex-direction: row-reverse;
		}
	}
	&__img {
		position: relative;
		width: 127px;
		height: 127px;
		border-radius: 50%;
		overflow: hidden;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	&__text-block {
		max-width: 355px;
		margin-left: 40px;
		&_rewers {
			margin-left: 0px;
			margin-right: 40px;
		}
	}
	&__title {
		font-weight: 600;
		font-size: 20px;
		line-height: 25px;
	}
	&__descr {
		margin-top: 10px;
		font-weight: 400;
		font-size: 17px;
		line-height: 22px;
		color: #999999;
	}
}

// .stage .stage .stage .stage .stage .stage .stage .stage .stage .stage .stage .stage
.stage {
	&__wrap {
		display: flex;
		justify-content: space-between;
		margin-top: 100px;
	}
}

.stage {
	&__left {
		flex: 0 0 302px;
		padding: 76px 32px 64px 32px;
		background: #ffffff;
		box-shadow: 0px 2.89522px 12.546px rgba(176, 176, 176, 0.35);
		border-radius: 19.3015px;
		margin-right: 25px;
	}
	&__descr-img {
		position: relative;
		width: 175px;
		height: 195px;
		overflow: hidden;
		margin: 0 auto;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__descr-text {
		margin-top: 73px;
		font-weight: 400;
		font-size: 22px;
		line-height: 27px;
		text-align: center;
		color: #808080;
	}
}

.stage {
	&__right {
		flex: 0 1 100%;
		max-width: 878px;
		margin-left: 25px;
	}
	&__sliders-block {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
}

